@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400..700;1,400..700&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: inherit;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: normal;
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

body {
  line-height: 1;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

picture,
video,
canvas,
svg {
  max-width: 100%;
  display: block;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  max-width: 100%;
}

*, html, body {
  font-family: "Montserrat", serif;
  font-size: 15px;
  color: #ffffff;
}

h1, h2, h3, h4, h4, h5 {
  font-family: "Montserrat", serif;
  font-weight: 700;
  margin-top: 30px;
  margin-bottom: 30px;
}

h1 {
  font-size: 40px;
  font-weight: 700;
}

h2 {
  font-size: 32px;
  font-weight: 700;
}

h3 {
  font-size: 24px;
  font-weight: 700;
}

p {
  margin-top: 30px;
  margin-bottom: 30px;
  font-size: 14px;
}

span {
  font-size: inherit;
}

/* Definir o layout geral */
.layout {
  display: flex;
  flex-direction: column; /* Organizar itens na vertical */
  height: 100vh; /* Altura total da janela */
  margin: 0;
}

/* Player no rodapé */
.player {
  background-color: #2ecc71;
  padding: 1rem;
  text-align: center;
}

/* Conteúdo principal entre o menu e o player */
.content {
  display: flex; /* Organizar itens na horizontal (sidebar e main) */
  flex: 1; /* Ocupa o espaço restante */
  overflow: hidden;
}

.main {
  background-color: #121212;
  flex: 1; /* Ocupa todo o espaço restante horizontalmente */
  width: 85vw;
}

.simplebar-scrollbar::before {
  background-color: #e9ff70;
  opacity: 1;
}

.simplebar-scrollbar.simplebar-visible:before {
  opacity: 1;
}

/* Top Menu */
.top-menu {
  background-color: #121212;
  height: 80px;
  border-bottom: 2px solid #272727;
  display: flex;
  align-items: center;
  padding-left: 30px;
  padding-right: 30px;
  justify-content: space-between;
}
.top-menu .login {
  display: flex;
  align-items: center;
  gap: 10px;
}
.top-menu .login a {
  color: #ffffff;
}
.top-menu .login a:hover {
  color: #e9ff70;
}
.top-menu .login .icon {
  fill: #ffffff;
}
.top-menu .login .icon.active {
  fill: #e9ff70;
}
.top-menu .nav {
  display: flex;
  align-items: center;
  gap: 10px;
}
.top-menu .nav ul {
  display: flex;
  gap: 10px;
}
.top-menu .nav ul li a {
  color: #ffffff;
}
.top-menu .nav ul li a:hover {
  color: #e9ff70;
}
.top-menu .searchform {
  background-color: #272727;
  border: 1px solid #272727;
  border-radius: 50px;
  padding: 11px 20px;
  display: flex;
  align-items: center;
}
.top-menu .searchform.active {
  border: 1px solid #e9ff70;
}
.top-menu .searchform input[type=text] {
  background-color: transparent;
  font-size: 14px;
  border: 0;
  outline: 0;
  color: #fff;
}
.top-menu .searchform #searchsubmit {
  height: 20px;
  width: 20px;
  border: 0;
  background-color: transparent;
  background-image: url("../images/icons/search.svg");
  background-size: 20px;
}
.top-menu .link-logo {
  line-height: 1;
}

.sidebar {
  background-color: #171717;
  width: 15vw; /* Largura fixa da sidebar */
  padding: 1rem;
  border-right: 2px solid #272727;
}
.sidebar .playlists-heading {
  background-image: url(../images/icons/library.svg);
  background-position: left center;
  background-repeat: no-repeat;
  height: 40px;
  padding-left: 36px;
  display: flex;
  align-items: center;
}
.sidebar .create-playlist-form {
  display: flex;
  background-color: #272727;
  padding: 3px 10px;
  padding-right: 0;
  border-radius: 3px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .create-playlist-form .create-playlist-form_input {
  background-color: transparent;
  outline: 0;
  border: 0;
  height: 30px;
  font-size: 14px;
}
.sidebar .create-playlist-form .create-playlist-form_button {
  width: 30px;
  height: 30px;
  background-image: url("../images/icons/add.svg");
  background-position: center;
  background-repeat: no-repeat;
  border: 0;
  background-color: transparent;
  transition: 300ms;
}
.sidebar .create-playlist-form .create-playlist-form_button:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.sidebar .playlists-list {
  padding: 10px;
  background-color: #272727;
  padding: 10px;
  margin-top: 20px;
  border-radius: 3px;
}
.sidebar .playlists-list li {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-top: 10px;
}
.sidebar .playlists-list li:first-child {
  margin-top: 0;
}
.sidebar .playlists-list li a {
  font-size: 13px;
  margin-right: auto;
}
.sidebar .playlists-list li a:hover {
  color: #e9ff70;
}
.sidebar .playlists-list li svg {
  height: 12px;
  width: 12px;
  fill: #fff;
}
.sidebar .playlists-list li:hover svg {
  fill: #e9ff70;
}
.sidebar .playlists-list li .trash {
  border: 0;
  background-color: transparent;
  opacity: 0.5;
}
.sidebar .playlists-list li .trash:hover {
  opacity: 1;
  scale: 1.1;
  cursor: pointer;
}
.sidebar .playlists-list li .trash svg {
  height: 16px;
  width: 16px;
}

.player {
  background-color: #171717;
  border-top: 2px solid #272727;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.player .info {
  display: flex;
  align-items: center;
  gap: 10px;
}
.player .info .current-thumb {
  height: 60px;
}
.player .info .info-group {
  display: flex;
  gap: 5px;
  flex-direction: column;
  align-items: flex-start;
}
.player .info .info-group .current-title-artist {
  font-size: 13px;
  display: flex;
  align-items: center;
}
.player .info .info-group .current-genres {
  font-size: 11px;
  display: flex;
  gap: 3px;
}
.player .info .info-group .current-genres .genre {
  border: 1px solid #444;
  background-color: #272727;
  padding: 3px 5px;
  border-radius: 3px;
}
.player .controls-and-time {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.player .controls-and-time .controls {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
}
.player .controls-and-time .controls .player_btn {
  position: relative;
}
.player .controls-and-time .controls .player_btn:hover .player_btn_hidden {
  opacity: 1;
  pointer-events: auto;
}
.player .controls-and-time .controls .player_btn .player_btn_hidden {
  position: absolute;
  left: 0;
  bottom: 32px;
  opacity: 0;
  pointer-events: none;
  background-color: transparent;
  z-index: 99;
  transition: 300ms;
  padding: 10px;
  padding-left: 0;
}
.player .controls-and-time .controls .player_btn .player_btn_hidden .player_btn_hidden_content form {
  background-color: #121212;
  width: 200px;
  border: 2px solid #272727;
  border-radius: 4px;
  padding: 10px;
}
.player .controls-and-time .controls .player_btn .player_btn_hidden .player_btn_hidden_content form .add_to_playlist {
  width: 100%;
  font-size: 12px;
  background-color: #272727;
  border-radius: 3px;
  padding: 4px;
}
.player .controls-and-time .controls .player_btn .player_btn_hidden .player_btn_hidden_content form fieldset {
  outline: 0;
  border: 0;
  height: 70px;
  margin-bottom: 10px;
}
.player .controls-and-time .controls .player_btn .player_btn_hidden .player_btn_hidden_content form fieldset .playlist-radio {
  display: flex;
  font-size: 12px;
  padding: 3px;
  border-radius: 3px;
  gap: 5px;
}
.player .controls-and-time .controls .player_btn .player_btn_hidden .player_btn_hidden_content form fieldset .playlist-radio:hover {
  background-color: #272727;
}
.player .controls-and-time .controls .player_btn button {
  height: 30px;
  width: 30px;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #121212;
  border-radius: 4px;
  border: 0;
}
.player .controls-and-time .controls .player_btn button:hover {
  background-color: #272727;
}
.player .controls-and-time .controls .player_btn button.add-to-playlist {
  background-image: url("../images/icons/playlist.svg");
}
.player .controls-and-time .controls .player_btn button.play-pause {
  background-image: url("../images/icons/play.svg");
}
.player .controls-and-time .controls .player_btn button.play-pause.active {
  background-image: url("../images/icons/pause.svg");
}
.player .controls-and-time .controls .player_btn button.previous {
  background-image: url("../images/icons/back.svg");
}
.player .controls-and-time .controls .player_btn button.random {
  background-image: url("../images/icons/random.svg");
}
.player .controls-and-time .controls .player_btn button.random.active {
  background-image: url("../images/icons/random-active.svg");
}
.player .controls-and-time .controls .player_btn button.loop {
  background-image: url("../images/icons/loop.svg");
}
.player .controls-and-time .controls .player_btn button.loop.active {
  background-image: url("../images/icons/loop-active.svg");
}
.player .controls-and-time .controls .player_btn button.next {
  background-image: url("../images/icons/next.svg");
}
.player .controls-and-time .controls .player_btn button.download {
  background-image: url("../images/icons/down.svg");
}
.player .controls-and-time .time-progress-group {
  display: flex;
  gap: 5px;
  align-items: center;
}
.player .controls-and-time .time-progress-group .current-time {
  font-size: 12px;
  min-width: 32px;
  display: flex;
  justify-content: flex-start;
}
.player .controls-and-time .time-progress-group .total-time {
  font-size: 12px;
}
.player .controls-and-time .time-progress-group .progress-bar-container {
  position: relative;
  width: 100%;
  height: 5px;
  background-color: #444;
  border-radius: 3px;
}
.player .controls-and-time .time-progress-group .progress-bar-container .progress-bar {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background-image: linear-gradient(to right, #cff40a, #e9ff70);
  width: 0%;
  border-radius: 3px;
  transition: width 0.1s;
}
.player .volume-group {
  display: flex;
  align-items: center;
  gap: 5px;
  /* Estilo para a barra */
}
.player .volume-group .volume-icon {
  width: 24px;
  height: 20px;
  background-image: url("../images/icons/volume.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.player .volume-group .volume-icon.zero {
  background-image: url("../images/icons/no-volume.svg");
  opacity: 50%;
}
.player .volume-group input[type=range] {
  -webkit-appearance: none; /* Remove o estilo padrão no Chrome e Safari */
  width: 100px;
  height: 5px;
  background: #e9ff70;
  border-radius: 4px;
  outline: none;
}
.player .volume-group input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 10px;
  height: 10px;
  background: #e9ff70;
  border-radius: 50%;
  cursor: pointer;
  border: 2px solid #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}
.player .volume-group input[type=range]::-moz-range-thumb {
  width: 10px;
  height: 10px;
  background: #e9ff70;
  border-radius: 50%;
  cursor: pointer;
  border: 2px solid #fff;
}

.sound-wave-player {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  gap: 2px;
  width: 20px; /* Quadrado */
  height: 10px; /* Quadrado */
  background-color: transparent;
}
.sound-wave-player.active .bar-player {
  animation: sound-player 1s infinite;
  height: 100%;
}
.sound-wave-player.active .bar-player:nth-child(1) {
  animation-delay: 0s;
}
.sound-wave-player.active .bar-player:nth-child(2) {
  animation-delay: 0.2s;
}
.sound-wave-player.active .bar-player:nth-child(3) {
  animation-delay: 0.4s;
}
.sound-wave-player.active .bar-player:nth-child(4) {
  animation-delay: 0.6s;
}

.bar-player {
  width: 1px;
  height: 100%;
  background-color: #e9ff70;
  animation: none;
  border-radius: 2px;
}

@keyframes sound-player {
  0%, 100% {
    transform: scaleY(0.3); /* Altura mínima da barra */
  }
  50% {
    transform: scaleY(1); /* Altura máxima da barra */
  }
}
.button {
  padding: 10px 20px;
  border-radius: 50px;
  background-color: #272727;
  color: #e9ff70;
  border: 1px solid #272727;
  font-size: 14px;
}
.button.reverse {
  background-color: #e9ff70;
  color: rgba(0, 0, 0, 0.6);
  border: 1px solid #e9ff70;
}
.button.reverse:hover {
  background-color: #272727;
  color: #e9ff70;
  border: 1px solid #272727;
}
.button:hover {
  background-color: #e9ff70;
  color: rgba(0, 0, 0, 0.6);
}

.top-banner {
  width: 100%;
  height: 400px;
  background-color: #666;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  padding: 40px;
  justify-content: center;
}
.top-banner.item-1 {
  background-image: url("../images/top-banner.jpg");
}
.top-banner.item-2 {
  background-image: url("../images/top-banner-2.jpg");
}
.top-banner.item-3 {
  background-image: url("../images/top-banner-3.jpg");
}
.top-banner h2 {
  color: #ffffff;
  font-size: 60px;
  font-family: "Lora", serif;
  font-weight: 700;
  text-align: center;
}
.top-banner p {
  font-weight: 700;
  font-size: 30px;
  text-align: center;
}
.top-banner .banner-link {
  text-align: center;
}
.top-banner a {
  cursor: pointer;
}

.splide__pagination__page.is-active {
  background: #e9ff70;
}

.container {
  padding: 40px;
  width: 100%;
  margin-top: 40px;
}
.container.p-t-0 {
  padding-top: 0;
}
.container.m-t-0 {
  margin-top: 0;
}

.song {
  background-color: #171717;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #171717;
  transition: 300ms;
}
.song:hover {
  transform: scale(1.05);
}
.song.active {
  border: 1px solid #e9ff70;
  background-color: #272727;
}
.song.active .thumb-container .sound-wave-container {
  display: flex;
}
.song.active:hover .thumb-container .sound-wave-container {
  display: none;
}
.song.active:hover .thumb-container .controls {
  display: flex;
}
.song.active .thumb-container .controls {
  display: none;
}
.song.active .thumb-container .controls .play-button {
  background-image: url("../images/icons/pause.svg");
}
.song .thumb-container {
  position: relative;
  width: 150px;
  height: 150px;
}
.song .thumb-container .thumb {
  width: 100%;
}
.song .thumb-container .controls {
  height: 150px;
  width: 150px;
  background-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.song .thumb-container .controls .play-button {
  height: 40px;
  width: 40px;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 2px;
  background-image: url("../images/icons/play.svg");
  background-repeat: no-repeat;
  border: 0;
  background-position: center;
  border-radius: 50px;
}
.song .thumb-container .sound-wave-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 150px;
  height: 150px;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.7);
  display: none;
}
.song .title-container {
  margin-top: 10px;
}
.song .title-container .title {
  font-size: 13px;
}
.song .artist-container {
  margin-top: 3px;
}
.song .artist-container .artist {
  font-size: 13px;
  display: flex;
  color: #e9ff70;
}
.song .artist-container .artist:hover {
  text-decoration: underline;
}
.sound-wave {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  gap: 6px;
  width: 50px; /* Quadrado */
  height: 50px; /* Quadrado */
  background-color: transparent;
}

.bar {
  width: 5px;
  height: 100%;
  background-color: #e9ff70;
  animation: sound 1s infinite;
  border-radius: 2px;
}

.bar:nth-child(1) {
  animation-delay: 0s;
}

.bar:nth-child(2) {
  animation-delay: 0.2s;
}

.bar:nth-child(3) {
  animation-delay: 0.4s;
}

.bar:nth-child(4) {
  animation-delay: 0.6s;
}

@keyframes sound {
  0%, 100% {
    transform: scaleY(0.3); /* Altura mínima da barra */
  }
  50% {
    transform: scaleY(1); /* Altura máxima da barra */
  }
}
.playlist {
  display: flex;
  gap: 10px;
}

.form-ui {
  background-color: #171717;
  padding: 40px;
}
.form-ui input[type=text], .form-ui input[type=email], .form-ui input[type=password] {
  height: 40px;
  border-radius: 40px;
  background-color: #272727;
  border: 2px solid #272727;
  padding: 10px;
  font-size: 14px;
  width: 300px;
  outline: 0;
}
.form-ui input[type=text]:focus, .form-ui input[type=email]:focus, .form-ui input[type=password]:focus {
  border: 2px solid #e9ff70;
}

.ui-tooltip {
  font-size: 11px;
  min-width: 80px;
  padding: 6px;
  background-color: #121212;
  border: 2px solid #272727;
  border-radius: 4px;
  position: absolute;
  left: -25px;
  bottom: 10px;
}